@import './variables.scss';

* {
  box-sizing: border-box;
}

html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  font-size: $defaultFontSize;
  font-family: $defaultFont;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

li,
ol,
ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}
.subtitle {
  display: block;
  margin: 0 auto;
  text-align: center;
  color: $brandColor;
}
button {
  height: 40px;
  padding: 0 10px;
  font-size: 17px;
  font-weight: 600;
  text-align: center;
  border-radius: 7px;
  color: white;
  border: none;
  background-color: $brandBgColor;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover {
    cursor: pointer;
    background-color: #32c220;
  }
}
// Ant modal
.ant-modal-close:hover {
  background-color: transparent;
  border: none;
}

//Slick slider
.slick-slider {
  width: 95px;

  .slick-list {
    overflow: hidden;

    .slick-track {
      max-height: 400px;
    }
  }
  .slick-arrow {
    width: 95px;
    height: 30px;
    margin-top: 10px;
    margin-bottom: 10px;
    border: none;
    background-color: $brandColor;

    &:hover {
      cursor: pointer;
    }
  }
}

.linkActive {
   .cart {
    fill: $activeColor;
  }
}

